.reviews {
    max-width: 1440px;
    width: 100%;
    margin: 40px auto 150px;
    padding: 0 12px;
    
    h2 {
        width: 480px;
        padding-bottom: 60px;
        margin-left: 80px;
    }
}

.reviews-titles{
    justify-content: center;
    gap: 30px;
     
    .btn {
        max-width: 1150px;
        height: 86px;

        &:hover {
            border-color: $stroke;
            color: $accent-hover;
        }
    }
}

.reviews-item {
    background: $system;
    border-radius: 15px;
    width: 560px;
    padding: 40px 50px;
    

    h5{
        font-weight: 500;
        padding-bottom: 20px;
    }

    p{
        font-weight: 400;
        font-size: 20px;
        padding-bottom: 20px;
    }

}