@media screen and (max-width: 1024px) {
    header {
        
        .container {
        padding: 11px 20px;
        }

        .navbar {
            padding: 0;
        }

        .navbar-brand {

            display: flex;

            img {
                max-height: 26px;
            }
        }
    }

    .login {

        .btn-user {
            width: 44px;
            height: 44px;
            padding: 4px 4px 7px;
            order: -1;
            background-color: #141810;
            color: $accent;
            border: none;
            position: relative;

            &::before{
                content: "";
                position: absolute;
                width: 8px;
                height: 8px;
                right: -2px;
                top: -2px;
                border-radius: 50%;
                background-color: $errors;
            }

            &.verefied::before {
                background-color: $accent;
            }
            
            &:hover, 
            &:focus {
                background-color: #141810;
                color: $accent;
            }
        }
        
        .navbar-collapse {
            .status {
                display: block;
            }
        }

        .navbar-nav_main-navigation {
            margin: 90px 0 128px; 
        }
    }

    .navbar-toggler {
        background-color: #141810;
        width: 44px;
        height: 44px;
        border-radius: 8px;
        position: relative;

        &:focus {
            box-shadow: none;
        }

        &__line {
            width: 20px;
            height: 2px;
            display: flex;
            background-color: $accent;
            position: absolute;
            left: 50%;
            top: 15px;
            transform: translate(-50%, 0);
            transition: .4s;
    
            &:nth-child(2) {
                top: 21px;
            }
    
            &:nth-child(3) {
                top: 27px;
            }
        }
    }

    
    [aria-expanded="true"] {

            .navbar-toggler__line {
                top: 19px;
                transform: translate(-50%, 0) rotate(45deg);

                &:nth-child(2) {
                    top: 19px;
                    transform: translate(-50%, 0) rotate(-45deg);
                }

                &:nth-child(3) {
                    opacity: 0;
                }
            }
    }

    .navbar-nav {
        .dropdown-menu {
            border: none;

            .dropdown-item {
                padding: 9px 25px;
                &::after {
                    display: none;
                }
            }
        }

        &_main-navigation {
            margin: 30px 0 128px;
            font-size: 20px;
        }
    }

    .navbar-collapse {
        margin: 0 -20px 0;
        padding: 0px 20px;
        z-index: 1;
        transform: translate(0, -66px);
        background-color: $background;

        .navbar-toggler {
            margin-top: 22px;
        }

        &::before {
            content: "";
            position: absolute;
            top: 75px;
            right: 0;
            left: 0;
            border: 1px solid #242424;
        }
        .status {
            position: absolute;
            left: 20px;
            top: 100px;
            display: none;
        }
        
    }

    .navbar-nav_country {
        position: absolute;
        right: 20px;
        top: 24px;
        
        .dropdown-menu {
            border: 1px solid $stroke;
            left: -10px;
            position: absolute;
        }

        .nav-link {
            font-size: 14px;
        }
    }

    .navbar-nav_language {
        right: 100px;
    }


    

    .advantage-template {
        padding-bottom: 60px;

        h2 {
            font-size: 32px;
        }

        &_text {
            font-size: 18px;
        }
    }

    .advantage-grid {
        display: grid;
        grid-gap: 20px
    }

    .advantages {
        padding: 100px 20px;
    }

    .advantage {
        align-items: center;
        flex-direction: column;

        .advantage-text {
            max-width: 100%;
            padding: 24px 24px 0 24px;
            .advantage-titles {
                font-size: 24px;
            }
    
            .advantage-subtitles{
                font-size: 16px;
            }
        }
    }

    picture {
       img {
        width: 240px;
        height: 272px;
       }
    }

    .step-temlate {
        padding-bottom: 60px;

        h2 {
            font-size: 32px;
            line-height: 40px;
        }

        h5 {
            font-size: 18px;
        }
    }


    .step {
        flex-direction: column;
        align-items: center;

        .step-item {
            width: 320px;
            padding: 24px;
            border-radius: 12px;

            .step-text {
                margin: 40px 0 12px;
                font-size: 24px;
            }
    
            h5,h6{
                font-size: 16px;
            }

            .step-mobile {
                display: block;
                margin-top: 40px;
                text-decoration: none;
            }
        }
    }



    input {
        padding: 22.5px 20px;
    }

    .input-row {
        display: block;
    }

    .input-box label {
        left: 21px;
        top: 24px;
    }
    
    input,
    label {
        font-size: 14px;
    }

    .column-logo {
        display: none;
    }

    .page-login .template-form {
        margin: 0 auto;
        padding: 0 20px;
        max-width: 500px;
    }

    .template-form {
        .btn-profile {
            position: absolute;
            left: 20px;
            top: 20px;
        }

        &__titles {
            min-height: 72px;
            font-size: 32px;
            padding: 11px 0 0 72px;
            display: flex;
            align-items: center;
        }
    
        &__subtitles {
            font-size: 18px;
            line-height: 1.5;
        }
    }

    .input-box {
        margin-bottom: 20px;
    }

    .control-box {
        margin: 44px 0;
    }

    .control-box label {
        font-size: 14px;
    }

    .template-form .btn-row {
        margin: 40px 0 20px;
    }

    .template-form__questions {
        font-size: 14px;
    }

    .btn {
        font-size: 16px;

        &.btn-lager{
            border-radius: 8px;
            padding: 21px 94px 23px;
        }
    }

    .hideShowPassword-toggle {
        width: 18px;
        height: 18px;
    }

    .nav {
        width: 50px;
        height: 50px;
        bottom: 20px;
        left: 20px;
        font-size: 14px;
    }

    .footer-logo {
        position: relative;
         &__link{
            position: absolute;
            left: 0;
            top: 0;

            img {
                width: 143px;
            }
         }
        .socials {
            gap: 10px;
            justify-content: flex-end;
        }
    }

    .btn {
        &.icon {
            width: 40px;
            height: 40px;
        }
    }

    .footer-row {
        padding: 40px 20px;
    }
    .footer-row,
    .footer-info {
        display: block;
    }

    .footer-menu {

        .link-list {
            padding: 12px 0 28px 0;
        }

        .contacts-list {
            padding: 12px 0 28px 0;

            .link-item {
                font-size: 16px;
            }
        }


        h5 {
            font-size: 14px;
        }
    }


    .footer-copyright {
        font-size: 14px;

        a {
           display: none;
        }
    }

    .footer-mobile {
        display: block;
        padding-bottom: 28px;
        
        p {
            font-size: 14px;
            color: $body-text;
        }

        a {
            display: flex;
            font-size: 20px;
            text-decoration: none;
            color: $titles;
        }
    }

    .model {
        padding: 20px;

        h1,
        h2 {
            font-size: 32px;
            line-height: 40px;
        }
        
        h3 {
            font-size: 24px;
            line-height: 32px;
        }

        h5 {
            font-size: 20px;
            line-height: 32px;
        }

        p{
            font-size: 16px;
        }
    }

    .reviews {

        h2 {
            font-size: 32px;
            line-height: 40px;
        }
    }

    .reviews-item {
        background: $background;
        border: 2px solid $stroke;
        border-radius: 15px;
        padding: 24px;

        h5{
            font-size: 20px;
            line-height: 32px;
        }

        p{
            font-size: 16px;
            line-height: 24px;
            padding-bottom: 0px;
        }
    }
    
    .reviews-titles{
        .btn {
            height: 70px;
        }
    }

    .cities {
        padding: 60px 20px;
    }

    .city-title {

        h1{
            font-size: 32px;
            line-height: 40px;
        }

        a {
            display: none;
        }
    }

    .city {
        
        .city-kiev,
        .city-kharkiv,
        .city-lviv,
        .city-odesa {
            padding: 32px 24px;
            
            &:hover {
                background-image: none;
                animation: none;
            }
        }

        &:nth-child(5) {
            border-bottom: 1px solid $stroke;
        }
    }

    .city-mobile {
        display: block;
        margin-top: 40px;
    }

    .questions {
        padding: 0 20px;
        .container {
            width: 320px;

            nav.socials {
                gap: 12px;
                padding-bottom: 100px;
            }
        }

        h2 {
            font-family: 32px;
            line-height: 40px;
        }

        h5{
            padding: 16px 0 40px;
            font-size: 18px;
            line-height: 26px;
        }
    }

    .faq-title {
        padding-bottom: 60px;

        h2 {
            font-size: 32px;
        }

        h5{
            font-size: 18px;
        }

        .transfer {
            display: none;
        }
    }
    .accordion-control {
        .accordion-button {
            max-width: 276px;
            font-size: 20px;
            line-height: 32px;
            font-weight: 400;

            &::after {
                top: 4px;
                right: -48px;   
            }

            &::before {
                top: 4px;
                right: -48px;
            }
        }
    }
}