@media screen and (max-width: 1199px) {
    

    .navbar-nav_main-navigation {
        position: static;
        transform: none;
        margin: 0 auto;
    }

    .navbar-nav_language {
        padding: 0 15px; 
    }

    .navbar-nav .nav-item {
        padding: 0px 5px; 
    }

    .page-login .template-form{
        
            margin: 97px auto 100px 500px;

    }
    
    .navbar-brand {
        margin-right: 12px; 
    }

    .footer-menu,
    .text-widget {
        padding: 0 5px;
    }

    .model {
        padding-top: 30px;
        .btn-profile {
            display: none;
        }
    }
    .reviews {
        justify-content: center;
        
        h2 {
            margin: 0px;
        }
    }
    .reviews-titles {
        display: flex;
        justify-content: center;
    }

    .reviews-titles{
        justify-content: center;
        gap: 30px;
         
        .btn {
            max-width: 560px;
            height: 86px;
    
            &:hover {
                border-color: $stroke;
                color: $accent-hover;
            }
        }
    }

}
