header {
    background-color: #373737;
    transition: 0.3s;
    z-index: 10;
    &.fixed{
        backdrop-filter: blur(24.5px);
        background: transparent;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;

        + .wrapper {
            padding-top: 156px;
        }
    }
    .navbar {
        padding: 0;
        
        &-brand {
            padding: 0;
            margin-right: 22px;
        }
    }

    .container {
        max-width: 1920px;
        padding: 50px;
        position: relative;
    }

    .nav-link {
        color: $titles;

        &:hover,
        &.dropdown-toggle.show {
            color: $accent;
            text-decoration: none;

            &:after {
                border-color: $accent;
            }
        }
    }
}

.btn-row {
    .btn-registration {
        text-decoration: none;
        padding: 9px 19px;
    }
}

.login {
    .btn-registration {
        display: none;
    }

    .btn-user,
    .btn-exit {
        display: block;
    }

    .btn-user {
        width: 56px;
        height: 56px;
        padding: 5px 5px 7px;
        display: flex;
        justify-content: center;
        align-items: center;
        text-decoration: none;
    }
}

.btn-user,
.btn-exit {
    display: none;
}



.navbar-nav_country {
    .nav-link {
        color: $body-text;
        font-weight: 600;
        font-size: 18px;

        &:after {
            transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out;
        }
    }
}

.navbar-nav_main-navigation {
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}

.dropdown-toggle::after {
    display: inline-block;
    vertical-align: 2px;
    margin-left: 8px;
    border-top: 3px solid $body-text;
    border-right: 3px solid $body-text;
    border-bottom: none;
    border-left: none;
    width: 10px;
    height: 10px;
    transform: rotate(135deg);
}

.navbar-nav {
    
    .nav-item {
        padding: 0 15px;
    }

    .dropdown-menu {
        background: $background;
        border-radius: 8px;
        padding: 5px 0;
        border: 1px solid $stroke;
        .dropdown-item {
            color: $titles;
            padding: 20px 25px 21px;
            position: relative;

            &:hover {
                background-color: transparent;
                color: $accent;
                text-decoration: none;
            }

            &:after {
                content: "";
                position: absolute;
                left: 25px;
                right: 25px;
                bottom: 0;
                height: 1px;
                background-color: $stroke;
            }
        }

        li {
            &:last-child {
                .dropdown-item {
                    &::after {
                        display: none;
                    }
                }
            }
        }
    }
}

.navbar-nav_small {

    .dropdown-toggle {

        &::after {
            display: none; 
        }
    }

    .dropdown-menu {

        min-width: auto;
        
        .dropdown-item {
            padding: 8px 25px 9px;
        }
    }
}

.navbar-nav_medium {

    .dropdown-menu {

        min-width: auto;
        
        .dropdown-item {
            padding: 16px 25px 17px;
        }
    }
}

.navbar-nav_large {

    .dropdown-menu {

        min-width: auto;
        
        .dropdown-item {
            padding: 20px 25px 21px;
        }
    }
}


.navbar-nav_language {
    @extend .navbar-nav_country;

    padding: 0 30px;
}
