.form-exchange {
    max-width: 460px;
    input {
        border: none ;
        outline: none;
    }

    .input-row{
        background-color: $system;
        border-radius: 12px;
        color: $titles;
        font-size: 24px;
        padding: 36px;
        width: 100%;
        border: 1px solid $stroke;
        align-items: center;

        input {
            padding: 0;
        }
    }

    label {
        padding-bottom: 20px;
    }

    .give {
        padding-bottom: 30px;
    }
    .btn-lager {
        padding: 44px 124px;
        font-size: 24px;
    }
    
}
.exchange-info {
    padding: 12px 0 40px 0;
    &-item {
        display: flex;
    }

    &-panel {
        display: none;
    }

    &__title{
        font-size: 20px;
        padding: 0 20px 0 0;
    }

    &__value {
        font-size: 20px;
        color: $accent;
    }

    li{
        list-style-type: none;
    }
}
.nice-select {
    border: 1px solid $stroke;
    background: $system;
    padding: 16px 20px;
    height: 56px;
    font-size: 18px;
    font-weight: 600;
    display: flex;
    align-items: center ;
    min-width: 201px;

    &-search-box {
        display: none;

        &:hover{
            background-color: none;
        }
    }

    .nice-select-dropdown {
        background: none;

        .list {
            background-color: $system;
          
            padding: 0 24px;

            li {
                
                border-bottom: 1px solid $stroke;
                font-size: 18px;
                padding: 20px 0;
            }

            img {
                display: none;

            }
        }

    }
    
    &::after {
        border-bottom: 2px solid $body-text;
        border-right: 2px solid $body-text;
        height: 10px;
        width: 10px;
        right: 20px;
        top: 50%;
    }

    .option {
        
        &:hover {
            background: none;
            color: $accent;
        }
    }

    .list {
        
    }
}
.nice-select .option.focus, 
.nice-select .option.selected.focus {
    background: none;
}