footer {
    margin-top: auto;
}

.footer-row {
    width: 1440px;
    max-width: 100%;
    margin: 0 auto;
    padding: 100px 12px;
    justify-content: center;
    border-top: 1px solid #242424;
}

.footer-menu {
    padding: 0 12px;
    
    li {
        list-style-type: none;
        padding: 10px 0;
    }

    ul {
        padding: 30px 0;
    }
}


.footer-logo .socials {
    display: flex;
    gap: 30px;
    margin: 40px  0;
    
}

.link-item {
    font-weight: 500;
    font-size: 20px;
    line-height: 25px;
    color: #F7FFEE;
    display: flex;
    align-items: center;
    text-decoration: none;
    white-space: nowrap;

    &:hover {
        background-color: transparent;
        color: $accent;
        text-decoration: none;
    }
}

.link-list {
    align-items: center;
    text-decoration: none;
}

.footer-logo__link {
    max-width: 206px;
    display: block;
}

.footer-row,
.footer-info {
    display: flex;
}

.footer-info {
    justify-content: space-between;
    width: 100%;

    h5 {
        font-size: 24px;
        color: $stroke;
    }
}

.footer-logo {
    padding: 0 24px 0 0;
    width: 100%;
    max-width: 368px;

    &__link {
        max-width: 206px;
        display: block;
    }
}


.footer-copyright {
    max-width: 1440px;
    margin: 0 auto;
    display: flex;
    padding: 38px 12px;
    position: relative;
    border-top: 1px solid #242424;

    a {
        margin-right: 40px;
        color: $stroke;
        text-decoration: none;

        &:hover {
            color: $accent;
        }
    }

    nav {
        margin-left: auto;
    }
    span {
        font-size: 16px;
        line-height: 20px;
    }

}

.footer-mobile {
    display: none;
}