body{
    background-color: $background;
    color: $stroke;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}
h1,h2,h3,h4,h5,h6{
    font-family: $font-primary;
    color: $titles;
    font-weight: 600;
}

.status {
    display: inline-block;
    border: 1px solid #242424;
    border-radius: 3px; 
    padding: 5px 25px 9px;
    line-height: 1;

    &.status-looks-at {
        color: $verefication;
    }

    &.status-verification-failed {
        color: $errors;
    }

    &.status-verified {
        color: $accent;
    }
}

.nav {
    background-color: $titles;
    color: $background;
    height: 75px;
    width: 75px;
    border-radius: 50%;
    position: fixed;
    bottom: 50px;
    left: 50px;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 800;
}

h1 {
    font-size: 58px;
    line-height: 70px;
}
h2 {
    font-size: 48px;
    line-height: 60px;
}
h3 {
    font-size: 36px;
    line-height: 34px;
}
h4 {
    font-size: 32px;
    line-height: 42px;
}
h5 {
    font-size: 24px;
    line-height: 20px;
}
h6 {
    font-size: 20px;
    line-height: 34px;
}


a {
    text-decoration: none;
    color: $accent;
    
    &:hover {
        text-decoration: underline;
    }
}

.fw-600 {
    font-family: $font-primary;
    font-weight: 600;
}

.btn{
    display: inline-block;
    border: 1px solid $stroke;
    border-radius: 5px;
    color: $titles;
    font-size: 18px;
    font-weight: 600;
    

    &.btn-primary{
        padding: 9px 61px;
        background-color: $accent;
        border-color: $accent-hover;
        color: $system;

        &:hover,
        &:active{
            border-color: $accent-hover;
            background-color: $accent-hover;
        }

        &:active,
        &:focus, 
        &:not(.btn-check)+.btn:active {
            color: $titles;
        }
    }

   &.btn-secondary{
        @extend .btn-primary;
        padding: 9px 52px;
        background-color: transparent;
        border-color: $stroke;
        color: $titles;

        &:hover {
            color: $system;
        }

        &:active,
        &:focus,
        &:not(.btn-check)+.btn:active {
            color: $titles;
        }
   }

    &.btn-lager{
        border-radius: 8px;
        padding: 27px 96px;
    }

    &.btn-profile{
        border-radius: 8px;
        padding: 8px 8px;
        width: 56px;
        height: 56px;
        position: relative;

        &:hover {
            border-color: $body-text;
        }

        &.verefied,
        &.no-verefied {
            &::before {
                content: "";
                position: absolute;
                width: 12px;
                height: 12px;
                right: -6px;
                top: -6px;
                border-radius: 50%;
                background-color: $accent;
            }
        }
        
        &.no-verefied{
            &::before{
                background-color: $errors;
            }
        }

        svg {
            width: 18px;
            height: 16px;
        }
    }

    &.icon {
        border-radius: 8px;
        padding: 8px 8px;
        width: 50px;
        height: 50px;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: #141810;
        border: none;
        
        svg {
            width: 22px;
            height: 23px;
        }
    }

    

    &:focus-visible{
        box-shadow: none;
    }

    &:not(.btn-check)+.btn:active {
        color: inherit;
        background-color: $accent-hover;
        border-color: $accent-hover;
    }
}

.wrapper {
    .container {
        max-width: 1464px;
        padding: 0 12px;
        margin: 0 auto;
    }
}

.advantage-grid{
    display: grid;
    grid-gap: 30px;
    grid-template-columns: 1fr 1fr;
}

.advantages,
.steps,
.cities,
.questions,
.FAQ,
.banner {
    padding-bottom: 250px;

}

.advantage-template {
    text-align: center;
    padding-bottom: 100px;
    
    &_text {
        padding-top: 20px;
        color: $body-text;
    }
}

picture {
    img {
     width: 100%;
     height: auto;
    }
 }

.advantage {
    display: flex;
    flex-shrink: 1;
    background: $system;
    border-radius: 15px;

    .advantage-text {
        width: 100%;
        max-width: 395px;
        padding: 40px;

        .advantage-titles {
            margin: 0 0 16px 0;
            
        }

        .advantage-subtitles {
            font-size: 20px;
            line-height: 1.7;
            color: $body-text;
            margin: 0;
        }
    }
}


.step-temlate {
    text-align: center;
    padding-bottom: 100px;

    h5 {
        margin-top: 30px;
        color: $body-text;
    }
}



.step {
    display: flex;
    gap: 30px;
    

    .step-item {
        width: 460px;
        padding: 50px;
        border-radius: 15px;
        background-color: $accent;
        

        .step-text{
            color: $system;
            margin: 50px 0 30px;
        }

        h5,h6 {
            color: $accent-hover-primary;
        }

        .step-mobile {
            display: none;
            color: $system;
            font-weight: 500;

            
            }
        }
}

.city-title {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    padding-bottom: 100px;
    border-bottom: 1px solid $stroke;


    a{
        font-size: 20px;
        text-decoration: none;
        color: $stroke;
    }
}

.city {
    display: flex;
    flex-direction: column;
    border-bottom: 1px solid $stroke;
    
    &:first-child {
        border-top: 1px solid $stroke;
    }

    a{
        font-size: 32px;
        text-decoration: none;
        color: $titles;
    }
    .city-kiev {
        padding: 60px 100px; 
        &:hover {
            background: url(./assets/img/kyiv.png) center/cover no-repeat;
            
            animation: animation-cities 18s infinite;
        }
    }

    .city-kharkiv {
        padding: 60px 100px;
        &:hover {
            background: url(./assets/img/kharkiv.png) center/cover no-repeat;
            animation: animation-cities 18s infinite;
        }
    }

    .city-lviv {
        padding: 60px 100px;
        &:hover {
            background: url(./assets/img/lviv.png) center/cover no-repeat;
            animation: animation-cities 18s infinite;
        }
    }

    .city-odesa {
        padding: 60px 100px;
        &:hover {
            background: url(./assets/img/odesa.png) center/cover no-repeat;
            animation: animation-cities 18s infinite;
        }
    }
}

.city-map {
    margin-top: 50px;
    
    iframe {
        width: 100%;
    }
}

.city-mobile {
    display: none;
}

@keyframes animation-cities {
    0% {
       background-position-y: 100%;
       background-position-y: bottom;
      }
    5% {
      background-position-y: 100%;
      background-position-y: bottom;
    }
    40% {
      background-position-y: top;
    }
    50% {
      background-position-y: top;
    }
    100% {
      background-position-y: bottom;
    }
}

.questions {
    text-align: center;
    .container {
        width: 705px;

        nav.socials {
            display: flex;
            gap: 28px;
            justify-content: center;
        }
    }

    h5 {
        padding: 30px 0 40px;
        font-weight: 400;
        color: $body-text;
    }
}


.faq-title {
    text-align: center;
    padding-bottom: 100px;

    h5 {
        margin-top: 30px;
        line-height: 36px;
        color: $body-text;
    }
}


.accordion-item {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding: 46px 0;
    border-bottom: 1px solid $stroke;
    
    &:first-child {
        border-top: 1px solid $stroke;
    }

}

.accordion-control {
    display: flex;
    width: 100%;
    align-items: center;
    position: relative;
    color: $titles;
    font-weight: 600;
    font-family: $font-primary;



    .collapsed {
        &:hover {
            &::after {
                transition: 0.3s;
                transform: rotate(0deg);
            }

            &::before {
                transition: 0.3s;
                transform: rotate(-90deg);
            }
        }
    }

    .accordion-button {
        font-size: 24px;
        font-weight: 600;

        

        &::after {
            content: "";
            width: 27px;
            height: 3px;
            background-color: $accent;
            transform: rotate(90deg);
            top: 18px;
            right: 18px;
            position: absolute;
        }
    
        &::before {
            content: "";
            width: 27px;
            height: 3px;

            background-color: $accent;
            top: 18px;
            right: 18px;
            position: absolute;
    
        }

        
    }

    &:hover {
        cursor: pointer;
        color: $accent;
    }

    [aria-expanded="true"] {
        &::after {
            transition: 0.6s;
            transform: rotate(0deg);
            
        }
    }
}   
