label {
    font-size: 20px;
}
input {
    background-color: $system;
    border: 1px solid $stroke;
    border-radius: 8px;
    color: $titles;
    font-size: 20px;
    padding: 26.5px 30px;
    width: 100%;

    &::-webkit-outer-spin-button,
    &::-webkit-inner-spin-button {
        display: none;
        -webkit-appearance: none;
        margin: 0;
    }

    &[type='number'] {
        -moz-appearance: textfield;
    }

    &:hover {
        border-color: $body-text;
    }
}



.column-logo{
    position: fixed;
    width: 333px;
    background-color: $accent;
    height: 100vh;
    padding: 40px;
    left: 0;
    top: 0;
}

.input-box {
    display: flex;
    flex-direction: column;
    margin-bottom: 30px;
    width: 100%;
    position: relative;
    label {
        position: absolute;
        left: 30px;
        top: 26.5px;
    }
}

.template-form{
    margin: 0 auto;
    padding: 0 78px 0 12px;
    max-width: 803px;
    position: relative;
    .btn-profile{
        position: absolute;
        left: -111px;
        top: 3px;
    }

    &__titles {
        margin-bottom: 23px;
    }

    &__subtitles {
        color: $body-text;
        margin-bottom: 40px;
    }

    .btn-row {
        margin-bottom: 40px;
        .btn {
            width: 100%;
        }
    }
    &__questions {
        max-width: 471px;
        width: 100%;
        text-align: center;
        margin: 0 auto;
        padding-bottom: 20px;
        font-size: 20px;
    }
}


.page-login {
    .template-form {
        margin: 97px auto 100px 596px;
    }
}

.input-row {
   display: flex;
    gap: 30px;
}

.control-box {
    margin: 42px 0;

    label {
        position: relative;
        padding-left: 40px;
        font-size: 16px;
    }

    input{
        display: none;
        + label {
            &::before {
                content: "";
                width: 26px;
                height: 26px;
                position: absolute;
                left: 0;
                top: 50%;
                transform: translate(0, -50%);
                background-color: $system;
                border: 1px solid $stroke;
                border-radius: 4px;
            }
        }
        &:checked,
        &:hover {
            + label {
                &::before{
                    content: "";
                    background-color: $controll-btn-bg;
                    
                }
            }
        }

        &:checked {
            + label {
                &::before{
                    background-image: url("data:image/svg+xml,%3Csvg width='13' height='10' viewBox='0 0 13 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 4L5 8L12 1' stroke='%23B1FF5C' style='stroke:%23B1FF5C;stroke:color(display-p3 0.6947 1.0000 0.3625);stroke-opacity:1;' stroke-width='2'/%3E%3C/svg%3E%0A");
                    background-repeat: no-repeat;
                    background-position: center;
                }
            }
        }
    }
}

.hideShowPassword-toggle {
    background: url(./assets/img/icon-eye.svg) center/contain no-repeat transparent;
    cursor: pointer;
    width: 24px;
    height: 24px;
    transform: translate(-30px, 0);
    font-size: 0;
    border: none;
}

.hideShowPassword-toggle-hide {
    background: url(./assets/img/icon-eye-active.svg) center/contain no-repeat transparent;
}